body {
  color: wheat;
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.field {
  grid-template-columns: repeat(var(--cols), 1fr);
  grid-template-rows: repeat(var(--rows), 1fr);
  gap: 3vw;
  width: min(90vw, 90vh);
  height: min(90vw, 90vh);
  display: grid;
}

.tile {
  -webkit-user-select: none;
  user-select: none;
  background-color: #333;
  border-radius: 3vw;
  justify-content: center;
  align-items: center;
  font-size: 10vw;
  display: flex;
  box-shadow: 0 0 6px 5px #e0ca23;
}

.green {
  background-color: green;
}
